import { forwardRef, type ComponentPropsWithoutRef } from "react"
import { buttonCva, ButtonVariants } from "./style"
import Slot from "../Slot/Slot"
import { css } from "jh-generated/css"

type ButtonProps = ButtonVariants &
  ComponentPropsWithoutRef<"button"> & { as?: boolean }

export const Button = forwardRef<HTMLButtonElement, ButtonProps>(
  ({ as, id, className, children, disabled = false, ...rest }, ref) => {
    const Comp = as ? Slot : "button"

    return (
      <Comp
        ref={ref}
        className={css(buttonCva.raw({ variant: "primary" }), {
          color: "text_danger",
        })}
        disabled={disabled}
        role="button"
        id={id}
        data-testid={id}
        {...rest}
      >
        {children}
      </Comp>
    )
  },
)
